import { BentoBox } from "@/components/BentoBox";
import React from "react";
import { v4 as uuidv4 } from "uuid";

interface BentoGridProps {
  children: React.ReactNode;
  className?: string;
  isLoading?: boolean;
}

export default function BentoGrid({
  children,
  className = "",
  isLoading = false,
}: BentoGridProps) {
  if (isLoading) {
    return (
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4 p-4">
        {[...Array(6)].map((_, i) => (
          <BentoBox
            key={i}
            isLoading={true}
            className={i === 0 ? "sm:col-span-2 sm:row-span-2" : ""}
          />
        ))}
      </div>
    );
  }

  return (
    <div
      className={`grid grid-cols-1 md:grid-cols-3 gap-6 p-6 ${className}`}
    >
      {React.Children.map(children, (child, index) => {
        if (!React.isValidElement(child)) return null;

        const layoutClasses = [
          "md:col-span-2", // PFP (wide)
          "md:row-span-2", // About Me (tall)
          "md:col-span-2", // Social (wide)
        ][index] || "";

        return (
          <div key={uuidv4()} className={layoutClasses}>
            {child}
          </div>
        );
      })}
    </div>
  );
}
