import BentoGrid from "@/components/BentoGrid";
import { createClient } from "@/prismicio";
import AboutMeSlice from "@/slices/AboutMeSlice";
import PfpSlice from "@/slices/PfpSlice";
import SocialSlice from "@/slices/SocialSlice";
import type { Metadata } from "next";

export default async function Home() {
  const client = createClient();
  const page = await client.getSingle("home");

  return (
    <main className="max-w-7xl mx-auto">
      <BentoGrid>
        {/* Pass individual slices directly instead of SliceZone */}
        {page.data.slices.map((slice, i) => (
          <div key={slice.id}>
            {/* Pass slice directly to respective components */}
            {slice.slice_type === "pfp" && <PfpSlice slice={slice} />}
            {slice.slice_type === "about_me" && <AboutMeSlice slice={slice} />}
            {slice.slice_type === "social" && <SocialSlice slice={slice} />}
          </div>
        ))}
      </BentoGrid>
    </main>
  );
}

export async function generateMetadata(): Promise<Metadata> {
  const client = createClient();
  const page = await client.getSingle("home");

  return {
    title: page.data.meta_title,
    description: page.data.meta_description,
  };
}
